import React, { useEffect, useState } from 'react';
import './Covid.css';

const Covid = () => {
    const[data,setData] = useState([]);
 
   const getCovidData = async () => {
      

       try{
           const response = await fetch('https://api.covid19india.org/data.json');
             const actualData = await response.json();
             console.log(actualData.statewise[0]);
             setData(actualData.statewise[0]);
       }catch(error){
           console.log(error);
       }
   }
 
    useEffect( ()=>{
     getCovidData();
  },[]);
    return (
        <>
        <section className="parent1">
           <h1>ðŸ”´ LIVE</h1> 
           <h2>COVID-19 CORONA-VIRUS TRACKER</h2>
           <div  className="parent">
           <ul>
               <li className="card">
                   <div className="card_main">
                       <div className="card_inner1">
                           <p className="card_name"><span>OUR </span>COUNTRY</p>
                           <p className="card_small">INDIA</p>
                       </div>

                   </div>
               </li>
               <li className="card">
                   <div className="card_main">
                       <div className="card_inner2">
                           <p className="card_name"><span>TOTAL</span> RECOVERED</p>
                           <p className="card_small">{data.recovered}</p>
                       </div>

                   </div>
               </li>
               <li className="card">
                   <div className="card_main">
                       <div className="card_inner3">
                           <p className="card_name"><span>TOTAL</span> CONFIRMED</p>
                           <p className="card_small">{data.confirmed}</p>
                       </div>

                   </div>
               </li>
               <li className="card">
                   <div className="card_main">
                       <div className="card_inner4">
                           <p className="card_name"><span>TOTAL</span> DEATH</p>
                           <p className="card_small">{data.deaths}</p>
                       </div>

                   </div>
               </li>
               <li className="card">
                   <div className="card_main">
                       <div className="card_inner5">
                           <p className="card_name"><span>TOTAL</span> ACTIVE</p>
                           <p className="card_small">{data.active}</p>
                       </div>

                   </div>
               </li>
               <li className="card">
                   <div className="card_main">
                       <div className="card_inner6">
                           <p className="card_name"><span>LAST</span> UPDATED</p>
                           <p className="card_small">{data.lastupdatedtime}</p>
                       </div>

                   </div>
               </li>
              
           </ul>
           </div>
        </section>
        </>
    )
}

export default Covid;
